.detail-panel {
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh;
    width: 400px;
    background: white;
    box-shadow: -2px 0 10px rgba(0,0,0,0.1);
    transform: translateX(100%);
    transition: transform 0.3s ease;
    z-index: 100;
    overflow-y: auto;
}

.detail-panel.active {
    transform: translateX(0);
}

.detail-header {
    padding: 20px;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f8f9fa;
}

.detail-content {
    padding: 20px;
}

.todo-detail-view {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.detail-section {
    margin-bottom: 20px;
}

.detail-section h4 {
    margin: 0 0 10px 0;
    font-size: 1.5em;
    color: #333;
}

.detail-section h5 {
    margin: 0 0 8px 0;
    color: #666;
    font-size: 0.9em;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.detail-section h5 i {
    color: #6c757d;
}

.detail-meta {
    display: flex;
    gap: 15px;
    margin-bottom: 15px;
}

.detail-priority {
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 0.8em;
    font-weight: 500;
}

.detail-priority.priority-high {
    background: #fde8e8;
    color: #dc2626;
}

.detail-priority.priority-medium {
    background: #fef3c7;
    color: #d97706;
}

.detail-priority.priority-low {
    background: #d1fae5;
    color: #059669;
}

.status-completed {
    color: #059669;
    display: flex;
    align-items: center;
    gap: 5px;
}

.status-pending {
    color: #d97706;
    display: flex;
    align-items: center;
    gap: 5px;
}

.detail-description, .detail-notes {
    background: #f8f9fa;
    padding: 15px;
    border-radius: 8px;
    border-left: 4px solid #4f46e5;
    margin: 0;
    line-height: 1.6;
}

.detail-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
}

.detail-actions {
    display: flex;
    gap: 10px;
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid #eee;
}

.edit-from-detail, .close-detail {
    flex: 1;
}

/* Responsive para mviles */
@media (max-width: 768px) {
    .detail-panel {
        width: 100%;
    }
    
    .detail-grid {
        grid-template-columns: 1fr;
    }
