aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,6BAA6B;AACjC;;AAEA;;;IAGI,WAAW;IACX,uBAAuB;IACvB,yBAAyB;IACzB,mCAAmC;IACnC,gCAAgC;IAChC,kCAAkC;AACtC;;AAEA;;;IAGI,aAAa;IACb,kCAAkC;IAClC,8CAA8C;AAClD;;AAEA;IACI,cAAc;IACd,6BAA6B;IAC7B,gBAAgB;IAChB,wBAAwB;AAC5B;;AAEA;IACI,6BAA6B;AACjC;;AAEA,eAAe;AACf;IACI;QACI,0BAA0B;IAC9B;;IAEA;QACI,UAAU;QACV,sBAAsB;IAC1B;AACJ;;AAEA,+CAA+C;;AAE/C;IACI,eAAe;IACf,QAAQ;IACR,MAAM;IACN,aAAa;IACb,YAAY;IACZ,iBAAiB;IACjB,uCAAuC;IACvC,2BAA2B;IAC3B,+BAA+B;IAC/B,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,gBAAgB;IAChB,yBAAyB;IACzB,qBAAqB;IACrB,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,kBAAkB;IAClB,8BAA8B;IAC9B,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,SAAS;AACb;;AAEA;IACI,aAAa;IACb,SAAS;IACT,gBAAgB;IAChB,iBAAiB;IACjB,0BAA0B;AAC9B;;AAEA;IACI,OAAO;AACX;;AAEA,4BAA4B;AAC5B;IACI;QACI,WAAW;IACf;;IAEA;QACI,0BAA0B;IAC9B;AACJ","sourcesContent":["/* Reset y variables CSS */\n:root {\n    /* Colores principales */\n    --primary-color: #667eea;\n    --primary-dark: #5a67d8;\n    --secondary-color: #764ba2;\n    --accent-color: #f093fb;\n\n    /* Colores neutros */\n    --text-color: #2d3748;\n    --text-light: #718096;\n    --bg-color: #ffffff;\n    --bg-light: #f7fafc;\n    --bg-dark: #1a202c;\n\n    /* Colores de estado */\n    --success-color: #48bb78;\n    --warning-color: #ed8936;\n    --error-color: #f56565;\n    --info-color: #4299e1;\n\n    /* Tipografa */\n    --font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n    --font-size-xs: 0.75rem;\n    --font-size-sm: 0.875rem;\n    --font-size-base: 1rem;\n    --font-size-lg: 1.125rem;\n    --font-size-xl: 1.25rem;\n    --font-size-2xl: 1.5rem;\n    --font-size-3xl: 1.875rem;\n\n    /* Espaciado */\n    --space-1: 0.25rem;\n    --space-2: 0.5rem;\n    --space-3: 0.75rem;\n    --space-4: 1rem;\n    --space-5: 1.25rem;\n    --space-6: 1.5rem;\n    --space-8: 2rem;\n    --space-10: 2.5rem;\n    --space-12: 3rem;\n\n    /* Bordes y sombras */\n    --border-radius: 8px;\n    --border-radius-lg: 12px;\n    --border-radius-xl: 16px;\n    --box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);\n    --box-shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);\n    --box-shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);\n\n    /* Transiciones */\n    --transition: all 0.3s ease;\n    --transition-fast: all 0.15s ease;\n    --transition-slow: all 0.5s ease;\n}\n\n/* Reset */\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n*::before,\n*::after {\n    box-sizing: inherit;\n}\n\nhtml {\n    scroll-behavior: smooth;\n}\n\nbody {\n    font-family: var(--font-family);\n    font-size: var(--font-size-base);\n    line-height: 1.6;\n    color: var(--text-color);\n    background: linear-gradient(135deg, var(--bg-light) 0%, #e3f2fd 100%);\n    min-height: 100vh;\n}\n\n/* Utilidades de texto */\n.text-center {\n    text-align: center;\n}\n\n.text-left {\n    text-align: left;\n}\n\n.text-right {\n    text-align: right;\n}\n\n.text-primary {\n    color: var(--primary-color);\n}\n\n.text-secondary {\n    color: var(--secondary-color);\n}\n\n.text-success {\n    color: var(--success-color);\n}\n\n.text-warning {\n    color: var(--warning-color);\n}\n\n.text-error {\n    color: var(--error-color);\n}\n\n/* Utilidades de espaciado */\n.mt-1 {\n    margin-top: var(--space-1);\n}\n\n.mt-2 {\n    margin-top: var(--space-2);\n}\n\n.mt-4 {\n    margin-top: var(--space-4);\n}\n\n.mt-8 {\n    margin-top: var(--space-8);\n}\n\n.mb-1 {\n    margin-bottom: var(--space-1);\n}\n\n.mb-2 {\n    margin-bottom: var(--space-2);\n}\n\n.mb-4 {\n    margin-bottom: var(--space-4);\n}\n\n.mb-8 {\n    margin-bottom: var(--space-8);\n}\n\n.p-1 {\n    padding: var(--space-1);\n}\n\n.p-2 {\n    padding: var(--space-2);\n}\n\n.p-4 {\n    padding: var(--space-4);\n}\n\n.p-8 {\n    padding: var(--space-8);\n}\n\n/* Contenedor principal */\n.main-content {\n    min-height: calc(100vh - 140px);\n    padding: var(--space-8) var(--space-4);\n}\n\n.container {\n    max-width: 1200px;\n    margin: 0 auto;\n    padding: 0 var(--space-4);\n}\n\n.container-sm {\n    max-width: 800px;\n    margin: 0 auto;\n    padding: 0 var(--space-4);\n}\n\n/* Loading */\n.loading {\n    text-align: center;\n    padding: var(--space-12) var(--space-4);\n}\n\n.spinner {\n    width: 40px;\n    height: 40px;\n    border: 4px solid var(--bg-light);\n    border-top: 4px solid var(--primary-color);\n    border-radius: 50%;\n    animation: spin 1s linear infinite;\n    margin: 0 auto var(--space-4);\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n.loading h1 {\n    color: var(--primary-color);\n    font-size: var(--font-size-3xl);\n    margin-bottom: var(--space-4);\n}\n\n/* Botones */\n.btn {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    gap: var(--space-2);\n    padding: var(--space-3) var(--space-6);\n    background: var(--primary-color);\n    color: white;\n    text-decoration: none;\n    border: none;\n    border-radius: var(--border-radius);\n    cursor: pointer;\n    transition: var(--transition);\n    font-weight: 500;\n    font-size: var(--font-size-base);\n    line-height: 1;\n}\n\n.btn:hover {\n    background: var(--primary-dark);\n    transform: translateY(-2px);\n    box-shadow: var(--box-shadow-md);\n}\n\n.btn:active {\n    transform: translateY(0);\n}\n\n.btn:disabled {\n    opacity: 0.6;\n    cursor: not-allowed;\n    transform: none;\n}\n\n.btn-secondary {\n    background: var(--secondary-color);\n}\n\n.btn-secondary:hover {\n    background: #6b46c1;\n}\n\n.btn-outline {\n    background: transparent;\n    border: 2px solid var(--primary-color);\n    color: var(--primary-color);\n}\n\n.btn-outline:hover {\n    background: var(--primary-color);\n    color: white;\n}\n\n.btn-sm {\n    padding: var(--space-2) var(--space-4);\n    font-size: var(--font-size-sm);\n}\n\n.btn-lg {\n    padding: var(--space-4) var(--space-8);\n    font-size: var(--font-size-lg);\n}\n\n/* Cards */\n.card {\n    background: var(--bg-color);\n    border-radius: var(--border-radius-lg);\n    padding: var(--space-6);\n    box-shadow: var(--box-shadow);\n    transition: var(--transition);\n    border: 1px solid #e2e8f0;\n}\n\n.card:hover {\n    transform: translateY(-5px);\n    box-shadow: var(--box-shadow-lg);\n}\n\n.card-header {\n    padding-bottom: var(--space-4);\n    margin-bottom: var(--space-4);\n    border-bottom: 1px solid #e2e8f0;\n}\n\n.card-title {\n    font-size: var(--font-size-xl);\n    font-weight: 600;\n    color: var(--text-color);\n    margin: 0;\n}\n\n/* Grid system */\n.grid {\n    display: grid;\n    gap: var(--space-6);\n}\n\n.grid-2 {\n    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n}\n\n.grid-3 {\n    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n}\n\n.grid-4 {\n    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n}\n\n/* Formularios */\n.form-group {\n    margin-bottom: var(--space-4);\n}\n\n.form-label {\n    display: block;\n    margin-bottom: var(--space-2);\n    font-weight: 500;\n    color: var(--text-color);\n}\n\n.form-input,\n.form-textarea,\n.form-select {\n    width: 100%;\n    padding: var(--space-3);\n    border: 2px solid #e2e8f0;\n    border-radius: var(--border-radius);\n    font-size: var(--font-size-base);\n    transition: var(--transition-fast);\n    background: var(--bg-color);\n}\n\n.form-input:focus,\n.form-textarea:focus,\n.form-select:focus {\n    outline: none;\n    border-color: var(--primary-color);\n    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);\n}\n\n.form-textarea {\n    resize: vertical;\n    min-height: 100px;\n}\n\n.form-error {\n    color: var(--error-color);\n    font-size: var(--font-size-sm);\n    margin-top: var(--space-1);\n}\n\n/* Alertas */\n.alert {\n    padding: var(--space-4);\n    border-radius: var(--border-radius);\n    margin-bottom: var(--space-4);\n    border-left: 4px solid;\n}\n\n.alert-success {\n    background: #f0fff4;\n    border-color: var(--success-color);\n    color: #22543d;\n}\n\n.alert-warning {\n    background: #fffaf0;\n    border-color: var(--warning-color);\n    color: #744210;\n}\n\n.alert-error {\n    background: #fed7d7;\n    border-color: var(--error-color);\n    color: #742a2a;\n}\n\n.alert-info {\n    background: #ebf8ff;\n    border-color: var(--info-color);\n    color: #1a365d;\n}\n\n/* Responsive */\n@media (max-width: 768px) {\n    .main-content {\n        padding: var(--space-4) var(--space-2);\n    }\n\n    .grid-2,\n    .grid-3,\n    .grid-4 {\n        grid-template-columns: 1fr;\n    }\n\n    .container {\n        padding: 0 var(--space-2);\n    }\n}\n\n@media (max-width: 480px) {\n    .btn {\n        width: 100%;\n        justify-content: center;\n    }\n\n    .loading h1 {\n        font-size: var(--font-size-2xl);\n    }\n}\n\n/* Modo oscuro (opcional) */\n@media (prefers-color-scheme: dark) {\n    :root {\n        --text-color: #e2e8f0;\n        --text-light: #a0aec0;\n        --bg-color: #1a202c;\n        --bg-light: #2d3748;\n    }\n\n    body {\n        background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);\n    }\n\n    .card {\n        background: #2d3748;\n        border-color: #4a5568;\n    }\n\n    .form-input,\n